.sessao {
    background-color: #33b4ff;
}

.convenios-container {
    padding-top: 90px;
}

.segundo-texto {
    background-color: blue;
    color: #fff;
    border: 1px solid blue;
    height: 40px;
}

.lista-planos li {
    border: 1px solid #fff;
    border-radius: 20px;
    width: 238px;
    height: 500px;
    background-color: #fff;
    margin: 20px;
    padding: 30px;
    list-style: none;
}

.limitar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 auto;
}

.lista-planos {
    display: flex;
    flex-wrap: wrap;
    border-radius: 9px;
}

.text-personal {
    color: rgb(0, 128, 255);
    font-family: sans-serif;
    text-transform: uppercase;
    line-height: 18px;
    text-decoration: none;
    font-weight: 600;
}

.money2 {
    color: black;
    font-size: 13px;
}

.money {
    text-align: justify;
    line-height: 18px;
    text-decoration: none;
    color: rgb(6, 207, 6);
    font-size: 30px;
}

.cta {
    text-align: right;
}

.btn-primary {
    color: #fff;
    border: 1px solid rgb(9, 157, 255);
    border-radius: 20px;
    background-color: rgb(9, 157, 255);
    text-decoration: none;
    margin: 5px;
    padding: 5px;
    list-style: none;
}

.pessoa {
    color: black;
    font-size: 13px;
}

.btn-primary:hover {
    box-shadow: 3px 3px 10px 6px rgb(5, 58, 231);
}

.text-title {
    color: black;
}

.titulo-sessao-v1 {
    color: black;
    font-size: 50px;
    text-align: left;
    text-transform: uppercase;
}

.titulo-sessao-v2 {
    color: rgb(0, 0, 0);
    font-size: 20px;
    text-align: left;
    text-transform: uppercase;
}

.titulo-central {
    color: black;
    font-size: 20px;
    text-align: center;
    text-transform: uppercase;
}