.formulario-container{
    display: flex;
    justify-content: center;
    margin:0 auto;
    padding: 60px;
    width: 55%;
}

.title{
    width: 50% auto;
    align-self: center;
}

.formulario{
    width: 50% auto;
    align-self: center;
}

.lista-formulario{
    list-style: none;
    text-align: right;
    border: 1px solid rgb(99, 99, 99);
    border-radius: 10px;
    background-color: rgb(202, 202, 202);
    margin: 15px;
    padding: 15px;
    list-style: none;
    text-transform: uppercase;
}

.lista-formulario li{
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 600;
    color: rgb(0, 162, 255);
}

.lista-formulario li input{
    width: 170px;
    padding: 0;
    height: 20px;
}

.lista-formulario li select{
    width: 172px;
    height: 20px;
}

.btn-agenda {
    color: #fff;
    border: 1px solid rgb(16, 123, 194);
    border-radius: 20px;
    background-color: rgb(18, 128, 201);
    text-decoration: none;
    margin: 100px;
    padding: 5px;
    list-style: none;
    justify-content: center;
}

.btn-agenda:hover {
    box-shadow: 3px 3px 10px 6px rgb(5, 58, 231);
}

.title-formulario{
    font-size: 20px;
    color: rgb(39, 180, 185);
}